    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

/* Полное покрытие экрана */
* {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}



/* Заголовок */
.title {
    font-size: 28px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #66fcf1;
    text-shadow: 0 0 10px rgba(102, 252, 241, 0.8);
    margin-bottom: 10px;
}

/* Подзаголовок */
.subtitle {
    font-size: 16px;
    font-weight: lighter;
    color: rgba(102, 252, 241, 0.8);
    margin-bottom: 20px;
}

/* Меню */
.menu {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* Кнопки меню */
.menu-button {
    background: linear-gradient(135deg, #3a0ca3, #7209b7);
    color: white;
    font-size: 16px;
    padding: 12px 20px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 0px 10px rgba(114, 9, 183, 0.5);
}

.menu-button:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 20px rgba(114, 9, 183, 0.8);
}

/* Анимация появления */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}
